import { forwardRef, ComponentProps, useRef, PropsWithChildren } from "react";
import styled from "styled-components";
import { color, typography, THEMES } from "../../components/shared/styles";
import { Label } from "../../general/typography";

const KINDS = {
  DEFAULT: "default",
  WITHCP: "withclipboard",
} as const;

const SIZES = {
  SMALL: "small",
  LARGE: "large",
  MEDIUM: "medium",
} as const;

interface IconProps {
  /** @deprecated FIXME:Rename to size*/
  appearance?: typeof SIZES[keyof typeof SIZES];
}

export interface ContainerProps {
  /**
   * Width wrapper.
   * **Note**: Enter the measurement unit **(px, %, em, rem)**
   */
  width?: string;
  block?: boolean;
  /** Margin vertical */
  mv?: number;
  /** Margin horizontal */
  mh?: number;
}

export interface InputProps {
  /** Id*/
  id?: string | undefined
  /** Label for input*/
  label?: string;
  /** */
  kind?: typeof KINDS[keyof typeof KINDS];
  /** Select a theme*/
  theme?: typeof THEMES[keyof typeof THEMES];
  /** @deprecated FIXME:Rename to size*/
  appearance?: typeof SIZES[keyof typeof SIZES];
}

const InputWrapper = styled.div<ContainerProps>`
  position: relative;
  width: ${p=>p.width};
  ${p=>p.mv&&`
    margin-top: ${p.mv}px;
    margin-bottom: ${p.mv}px;
  `}
  ${p=>p.mh&&`
    margin-left: ${p.mh}px;
    margin-right: ${p.mh}px;
  `}
  display: ${(p) => (p.block ? "block" : "inline-block")};
`;

const StyledInput = styled.input<InputProps>`
  width: 100%;
  outline: none;
  overflow: hidden;
  appearance: none;
  -moz-appearance: none;
  box-sizing: border-box;
  text-overflow: ellipsis;
  -webkit-appearance: none;
  color: ${color.grayscale.black};
  padding: ${(p) =>
    p.appearance == "large"
      ? "18px 20px"
      : p.appearance == "small"
      ? "5px 6px"
      : "10px 11px"};
  border-radius: 3px;
  color: ${color.grayscale.shark};
  font-family: ${typography.type.primary};
  border: 1px solid ${color.grayscale.dust};
  font-weight: ${typography.weight.regular};
  font-size: ${(p) =>
    p.appearance == "large"
      ? typography.size.m3
      : p.appearance == "small"
      ? typography.size.s3
      : typography.size.m1}px;
  &:not(:placeholder-shown)[type="password"]{
    font-size: 14px;
    letter-spacing: 1px;
    font-family: Verdana;
  }

  &:focus-visible,
  &:focus {
    -webkit-box-shadow: 0 0 2px
      ${(p: InputProps) => color.primary[p.theme ? p.theme : "default"]};
    outline: none;
    box-shadow: 0 0 2px
      ${(p: InputProps) => color.primary[p.theme ? p.theme : "default"]};
  }

  &::placeholder {
    color: ${color.grayscale.silver};
  }

  ${(p) =>
    p.kind === KINDS.WITHCP && p.appearance == "large"
      ? `padding-right: 45px;`
      : (p.kind === KINDS.WITHCP && p.appearance == "medium") ||
        p.appearance == "small"
      ? `padding-right: 30px;`
      : null};
`;

const InputIcon = styled.span<IconProps>`
  cursor: pointer;
  position: absolute;
  margin-left: ${(p) => (p.appearance == "large" ? "-30px" : "-20px")};
`;

export const Input = forwardRef<
  unknown,
  PropsWithChildren<
    InputProps & ContainerProps & JSX.IntrinsicElements["input"]
  >
>(
  (
    {
      id,
      mv,
      mh,
      block,
      label,
      width,
      required,
      children,
      kind = "default",
      theme = "default",
      appearance = SIZES.MEDIUM,
      ...rest
    },
    ref
  ) => {
    const inputRef = useRef(ref as ComponentProps<typeof StyledInput>["ref"]);
    return (
      <InputWrapper width={width} block={block} mv={mv} mh={mh}>
        {label && (
          <>
            <Label htmlFor={id}>{label}{required?"*":""}</Label>
            <br />
          </>
        )}
        <StyledInput
          id={id}
          name={id}
          theme={theme}
          appearance={appearance}
          required={required}
          kind={kind}
          {...rest}
          ref={inputRef}
        ></StyledInput>
        {kind === KINDS.WITHCP && (
          <InputIcon
            appearance={appearance}
            className="icon-clipboard"
            // FIXME:v0.2 Move this style into className
            style={{ top: `${label ? "calc(50% + 3px)" : "calc(22% + 3px)"}` }}
            onClick={(e) => copyToClipboard()}
          ></InputIcon>
        )}
      </InputWrapper>
    );

    function copyToClipboard() {
      var value = inputRef.current.value;
      navigator.clipboard.writeText(value)
    }
  }
);
