import styled from "styled-components";
import { Inav } from ".";

export const StyledHeader = styled.header`

`

export const StyledNav = styled.nav<Inav>`
  display: flex;
  ${(props) =>
    props.orientation === "vertical"
      ? `flex-direction: column;
        ${props.align === "center" ? `align-items:center;`:
          props.align === "split" ? `align-items:stretch;`:
          props.align === "end" ? `align-items:end;`:
          props.align === "start" && `align-items:start;`
        }`
      : `
        align-items: center;
        ${props.align === "center" ? `justify-content:center;`:
          props.align === "split" ? `justify-content:space-between;` :
          props.align === "end" ? `justify-content:end;`:
          `justify-content:start;`
        }
  `}
`;
  
export const StyledNavItem = styled.span`
  display: inherit;
  align-items:center;
`
