import {
  ComponentProps,
  forwardRef,
  PropsWithChildren,
  ReactElement,
  useState,
} from "react";
import styled from "styled-components";
import { color } from "../../components/shared/styles";

export interface WrapperLightboxProps {}

export interface LightboxProps {
  id?: string | undefined
  visible: boolean;
}

export interface RemoteControlProps {
  idLighbox: string;
}

const WrapperLighbox = styled.div<
  WrapperLightboxProps & { children: ReactElement }
>``;

const StyledLightbox = styled.div<LightboxProps & { children: ReactElement }>`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: ${color.secondary.nile}E6;
  z-index: 9;
  display: none;
  ${(props) =>
    props.visible &&
    `
    display:block;
  `};
`;


export const Lightbox = forwardRef<
  unknown,
  PropsWithChildren<
    WrapperLightboxProps & LightboxProps & JSX.IntrinsicElements["section"]
  >
>(({ children, id, visible = false, ...rest }, ref) => {
  const [useVisible, setVisible] = useState<boolean>(false)
  return (
    <WrapperLighbox
      {...rest}
      ref={ref as ComponentProps<typeof WrapperLighbox>["ref"]}
    >
      <>
        <StyledLightbox id={id} visible={useVisible || visible}>
          <>
            {children}
          </>
        </StyledLightbox>
      </>
    </WrapperLighbox>
  );

});
