import React, { HTMLAttributes } from 'react';
import { StyledNav, StyledHeader, StyledNavItem } from "./header.style";

export interface Ichildren{
  children?: any
}

export interface Inav{
  orientation?: "vertical" | "horizontal";
  align?: "start" | "center" | "end" | "split"
}

export function Header({children, ...rest}: Ichildren & Inav & HTMLAttributes<HTMLHeadElement>){
  return <StyledHeader {...rest} >{children}</StyledHeader>
}

export function Nav({children, ...rest} : Ichildren & Inav & HTMLAttributes<HTMLDivElement>){
  return <StyledNav {...rest} >{children}</StyledNav>
}

Nav.Item = function NavItem ({children, ...rest} : Ichildren & HTMLAttributes<HTMLSpanElement>){
  return <StyledNavItem {...rest} >{children}</StyledNavItem>
}

Nav.Section = function NavSection ({children, ...rest} : Ichildren & HTMLAttributes<HTMLSpanElement>){
  return <StyledNavItem {...rest} >{children}</StyledNavItem>
}